/**
 * Template Name: Marvel - HTML Template for Product Promotion
 * Author: rustheme
 * Theme color: $brand-primary
 */

// Theme Colors reference
//#ec6952 red (default)
//#28a5a8 teal
//#497cb1 blue
//#f89d29 orange
//#737f97 purple
//#f06060 pink-red

// Variables
$brand-primary: #ec6952;
$brand-secondary: #4a6781;
$darkBlue: #253340;

/* Scaffolding */
*,
*:before,
*:after {
	box-sizing: inherit;
}

html {
	font-size: 16px;
}

body {
	font-family: 'Poppins', Arial, sans-serif;
	font-weight: 300;
	font-size: 1rem;
	line-height: 1.6;
	color: #444;
	background-color: #fafafa;
	box-sizing: border-box;
	overflow-x: hidden;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

/* Headings */
h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
	font-family: inherit;
	font-weight: 500;
	line-height: 1.2;
	margin-top: 1.5rem;
	margin-bottom: .75rem;
}

h1, .h1,
h2, .h2,
h3, .h3 {
	letter-spacing: -.0375em;
}

/* Paragraphs */
p {
	margin-bottom: 1rem;
}

/* Lists */
ul,
ol {
	margin-bottom: 1rem;
}

/* Links */
a {
	color: $brand-primary;
	transition: .2s ease-out;
	transition-property: color, background-color, opacity;

	&:hover {
		text-decoration: underline;
		color: darken($brand-primary, 15%);
	}

	&:focus {
		text-decoration: none;
	}

	/* Remove this for better a11y */
	&:hover,
	&:focus,
	&:active {
		outline: none;
	}
}

/* Buttons */
.btn {
	font-family: 'Poppins', Arial, sans-serif;
	font-weight: 500;
	letter-spacing: -.01em;
	text-transform: uppercase;
	transition: .2s ease-out-sine;
	transition-property: color, opacity, background-color, border-color;

	&:active {
		box-shadow: none;
	}
}

.btn-lg {
	padding: .75em 1.5em;
}

.btn i {
	color: inherit;
	margin-right: 5px;
}

.btn-cta {
	padding: .75em 1.5em;
}

.btn-cta-primary {
	background-color: $brand-primary;
	border: 2px solid $brand-primary;
	color: #fff;

	&:hover,
	&:focus {
		background-color: darken($brand-primary, 10%);
		border-color: darken($brand-primary, 10%);
		color: #fff;
	}
}

.btn-cta-secondary {
	background-color: transparent;
	border: 2px solid;
	color: $brand-primary;

	&:hover,
	&:focus {
		background-color: $brand-primary;
		border-color: $brand-primary;
		color: #fff;
	}
}

.btn-ghost {
	padding: 8px 20px;
	background-color: transparent;
	color: #fff;
	border: 1px solid;

	&:hover,
	&:focus {
		background-color: darken($brand-secondary, 15%);
		border-color: darken($brand-secondary, 15%);
		color: #fff;
	}
}

.btn-ghost i {
	margin-right: 10px;
	font-size: 22px;
}

.btn-ghost .text {
	position: relative;
	top: -2px;
}

.figure {
	position: relative;
}

/* Code */
code {
	background-color: rgba($darkBlue, .05);
	color: lighten($darkBlue, 25%);
}

/* Pre */
pre {
	font-size: .85rem;
	background-color: #f7f8f9;
	border-color: #e5e5e5;
}

/* Forms */
.form-control {
	height: 40px;
	border-color: #e5e5e5;
	box-shadow: none;
	transition: .2s ease-out;
	transition-property: border-color, opacity;

	&:hover,
	&:focus {
		box-shadow: none;
	}

	&:focus {
		border-color: #e1e1e1;
	}
}

.form-control::-webkit-input-placeholder {
	color: #a6a6a6;
}

.form-control:-moz-placeholder {
	color: #a6a6a6;
}

.form-control::-moz-placeholder {
	color: #a6a6a6;
}

.form-control:-ms-input-placeholder {
	color: #a6a6a6;
}

.form-group + .form-group:last-child {
	margin-bottom: 0;
}

/* Inputs */
input[type="text"],
input[type="email"],
input[type="password"],
input[type="submit"],
input[type="button"],
textarea,
select {
	appearance: none;
	/* for mobile safari */
	-webkit-appearance: none;
}

/* Helper classes */
.highlight {
	color: $brand-primary;
}

.text-highlight {
	color: #952410;
}

.m-b-0 {
	margin-bottom: 0 !important;
}

.m-b {
	margin-bottom: 1rem !important;
}

.m-b-md {
	margin-bottom: 1.5rem !important;
}

.m-b-lg {
	margin-bottom: 3rem !important;
}

.m-b-xl {
	margin-bottom: 6rem !important;
}

.m-t-0 {
	margin-top: 0 !important;
}

.m-t {
	margin-top: 1rem !important;
}

.m-t-md {
	margin-top: 1.5rem !important;
}

.m-t-lg {
	margin-top: 3rem !important;
}

.m-r-0 {
	margin-right: 0 !important;
}

.m-r {
	margin-right: 1rem !important;
}

.m-r-md {
	margin-right: 1.5rem !important;
}

.m-r-lg {
	margin-right: 3rem !important;
}

.m-l-0 {
	margin-left: 0 !important;
}

.m-l {
	margin-left: 1rem !important;
}

.m-l-md {
	margin-left: 1.5rem !important;
}

.m-l-lg {
	margin-left: 3rem !important;
}

/*** Theme ***/
/* Layout */
.layout-canvas {
	margin: auto;
	background-color: #fff;
}

.layout-canvas-boxed {
	max-width: 1170px;
}

.layout-container {
	position: relative;
}

/* Video */
#topcontrol {
	display: inline-block;
	width: 35px;
	height: 35px;
	text-align: center;
	background-color: $brand-secondary;
	color: #fff;
	border: none;
	border-radius: 50%;
	transition: background-color .2s ease-out;
	z-index: 30;

	&:hover {
		background-color: #384d60;
	}
}

#topcontrol i {
	position: relative;
	top: 3px;
	font-size: 25px;
}

/* Background images */
.bg-img {
	position: absolute;
	width: 100%;
	top: 0;
	left: 0;
	background-repeat: no-repeat;
	background-position: 50% 50%;
	background-size: cover;
}

.bg-img > .container {
	position: relative;
}

.bg-img-fill {
	height: 100%;
}

.bg-overlay:before {
	content: '';
	display: block;
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	right: 0;
	left: 0;
	bottom: 0;
	background-color: $darkBlue;
	opacity: .4;
}

.bg-gray {
	background-color: #f7f8f9;
}

.bg-inverse {
	color: #fff;
}

/* Header */
.header {
	position: relative;
	width: 100%;
	height: 80px;
	z-index: 40;
	transition: .2s ease-out-sine;
	transition-property: background-color, box-shadow;

	@media screen and (min-width: 992px) {
		&.navbar-fixed-top {
			position: fixed;
		}
	}
}

.header .container {
	position: relative;
}

.header h1.logo {
	float: left;
	margin-top: 0;
	margin-bottom: 0;
	font-family: 'Lora', Times, serif;
	font-size: 24px;
	font-weight: 700;
	line-height: 1;
	text-transform: none;
}

.header h1.logo a {
	display: inline-block;
	max-width: 155px;
	position: relative;
	padding-top: 28px;
	padding-bottom: 28px;
	text-align: center;
	color: #fff;
	transition: color .2s ease-out-sine;

	&:hover,
	&:focus {
		outline: none;
		text-decoration: none;
	}
}

.header h1.logo .logo-title {
	vertical-align: middle;
	line-height: 1.6;
}

.header .main-nav {
	margin-top: 15px;
}

.header .main-nav .navbar-toggle {
	margin-right: 0;
	margin-top: 0;
	background: none;
	position: absolute;
	right: 15px;
	top: 50%;
	transform: translateY(-50%);

	&:focus {
		outline: none;
	}
}

.header .main-nav .navbar-toggle .icon-bar {
	background-color: rgba(255, 255, 255, .6);
	height: 3px;
}

.header .main-nav .navbar-toggle:hover .icon-bar {
	background-color: rgba(255, 255, 255, 1);
}

.header-blog .main-nav .navbar-toggle .icon-bar {
	background-color: rgba($darkBlue, .6);
}

.header-blog .main-nav .navbar-toggle:hover .icon-bar {
	background-color: rgba($darkBlue, 1);
}

.header .main-nav .nav .nav-item {
	margin-right: 15px;
	font-size: .85rem;
	font-weight: 400;
	text-transform: uppercase;
}

.header .main-nav .nav .nav-item.active > a {
	color: $brand-primary !important;
	background: none;
}

.header .main-nav .nav .nav-item.nav-item-cta a.btn-cta-secondary {
	margin: 5.5px 0;
	padding: 8px 15px;
	font-size: .85rem;
	font-weight: inherit;
	color: #fff;
	vertical-align: middle;
	border: 1px solid;

	&:hover {
		background-color: $brand-primary;
		border-color: $brand-primary;
	}
}

.header .main-nav .nav .nav-item a {
	color: #fff;

	&:hover,
	&:focus {
		color: $brand-primary;
		background: none;
	}
}

.header .main-nav .nav .nav-item.active {
	color: $brand-primary;
}

.header .main-nav .nav .nav-item.last {
	//margin-right: 0;
}

.header .main-nav .nav.active {
	position: relative;
}

.header .main-nav .nav.active > a {
	background-color: darken($brand-primary, 15%);
	color: #fff;
	color: rgba(255, 255, 255, 0.8);

	&:before {
		content: "\f0d8";
		position: absolute;
		bottom: -8px;
		left: 0;
		text-align: center;
		font-family: FontAwesome;
		display: block;
		font-size: 12px;
		width: 100%;
	}

	&.dropdown-toggle:before {
		display: none;
	}
}

.header .main-nav .nav.open a {
	background: none;
}

.header .main-nav .nav .dropdown-menu {
	border-radius: 4px;
	margin: 0;
	border: 0;
	text-transform: none;
	min-width: 180px;
	background-color: $darkBlue;

	@media screen and (max-width: 767px) {
		box-shadow: none;
	}

	&:before {
		content: '';
		display: block;
		width: 0;
		height: 0;
		border-left: 10px solid transparent;
		border-right: 10px solid transparent;
		border-bottom: 10px solid $darkBlue;
		position: absolute;
		right: 30px;
		top: -10px;

		@media screen and (max-width: 767px) {
			display: none;
		}
	}
}

.header .main-nav .nav .dropdown-menu a {
	border-bottom: 1px solid darken($darkBlue, 2.5%);
	padding: 8px 20px;
	color: #fff;
	opacity: 1;

	@media screen and (max-width: 767px) {
		border-color: transparent;
	}

	&:hover {
		background-color: lighten($darkBlue, 5%);
		color: #fff;
	}
}

.header .main-nav .nav .dropdown-menu .active > a {
	background-color: lighten($darkBlue, 5%);
}

/* Header scrolled */
.header.scrolled {
	top: 0;

	@media screen and (min-width: 992px) {
		background-color: #fff;
		z-index: 1000;
		height: 80px;
		top: 0;
		box-shadow: 0 2px 4px -1px rgba(0, 0, 0, .03), 0 4px 5px 0 rgba(0, 0, 0, .03), 0 1px 10px 0 rgba(0, 0, 0, .04);
	}
}

.header.scrolled h1.logo a {
	@media screen and (min-width: 992px) {
		color: $brand-primary;
		background-clip: padding-box;
		padding-left: 0;
		padding-right: 0;
	}
}

.header.scrolled .main-nav .nav > .nav-item > a {
	color: #666;
	opacity: 1;

	&:hover,
	&:focus {
		color: $brand-primary;
	}
}

.header.scrolled .main-nav .nav .nav-item.nav-item-cta a.btn-cta-secondary {
	background-color: $brand-primary;
	border-color: $brand-primary;

	&:hover,
	&:focus {
		background-color: transparent;
	}
}

.header.scrolled .main-nav .nav .dropdown-menu a {
	color: #fff;
}

.navbar-collapse {
	@media screen and (max-width: 767px) {
		border-top: none;
		box-shadow: none;
		width: 100%;
		left: 0;
		top: 80px;
		position: absolute;
		background-color: $darkBlue;
		z-index: 45;
	}

	@media screen and (min-width: 768px) {
		position: relative;
	}
}

.navbar-nav {
	@media screen and (max-width: 767px) {
		margin: 0;
		padding-top: 15px;
		padding-bottom: 15px;
	}
}

.navbar-collapse.collapse .navbar-nav {
	@media screen and (min-width: 768px) {
		float: none;
		margin: 0 auto;
		display: inline-block;
	}
}

/* Override bootstrap default */
.nav .open > a,
.nav .open > a:hover,
.nav .open > a:focus {
	background: none;
}

/* Carousel */
.flickity-viewport {
	width: 100%;
	height: 100%;
}

.carousel-cell {
	float: left;
	display: block;
	width: 100%;
	margin-right: 10px;
	background-position: center center;
	background-repeat: no-repeat;
	background-size: cover;
}

.carousel-cell .container {
	position: relative;
	height: 100%;
}

.carousel-caption {
	position: absolute;
	bottom: auto;
	top: 50%;
	left: 50%;
	text-shadow: none;
	transform: translate(-50%,-50%);
	width: 100%;

	@media screen and (min-width: 992px) {
		width: 80%;
	}
}

.carousel-caption > :first-child {
	margin-top: 0;
}

.carousel-caption > :last-child {
	margin-bottom: 0;
}

.carousel-caption h2 {
	margin-bottom: 1rem;
	font-size: 1.5rem;

	@media screen and (min-width: 768px) {
		font-size: 2rem;
	}

	@media screen and (min-width: 992px) {
		font-size: 2.5rem;
	}
}

.carousel-cell > a {
	display: block;
	width: 100%;
	height: 100%;
}

.blog .carousel-cell {
	height: 500px;
}

/* Sections */
.section {
	position: relative;
	padding-top: 4rem;
	padding-bottom: 4rem;
}

.section > .container > :last-child {
	margin-bottom: 0;
}

/* Promo */
.title {
	font-size: 2rem;
	margin-top: 0;
}

.section-promo > .container {
	position: absolute;
	top: 50%;
	right: 0;
	left: 0;
	transform: translateY(-50%);
}

.section-promo .title {
	margin-top: 0;
	font-size: 2.75rem;
	font-weight: 600;
}

.section-promo .intro {
	opacity: .7;
}

.section-promo .btn-link {
	color: rgba(255, 255, 255, .7);
	font-weight: 500;
	font-size: 12px;
	text-decoration: none;

	&:hover {
		color: rgba(255, 255, 255, 1);
		text-decoration: none;
	}

	&:focus {	
		outline: none;
	}
}

.section-promo .btn-link i {
	margin-right: 5px;
	font-size: 18px;
	position: relative;
	top: 2px;
}

/* Sections-wrapper */
.sections-wrapper {
	background-color: #fff;
	z-index: 20;
	position: relative;
}

.section .intro {
	margin-bottom: 1.5rem;
	font-size: 1.125rem;
}

/* Section About */
.section-about .intro {
	color: #999;
	margin-bottom: 30px;
	font-size: 18px;
}

.section-about .item {
	padding: 90px 0;
	border-bottom: 1px solid #eee;
}

.section-about .item.last-item {
	border-bottom: none;
}

.section-about .item .content {
	@media screen and (max-width: 767px) {
		margin-bottom: 60px;
	}
}

.section-about .item .title {
	margin-top: 0;
	margin-bottom: 1.5rem;
	color: darken($brand-secondary, 15%);
	font-size: 1.5rem;
	text-align: center;
	text-transform: none;

	@media screen and (min-width: 992px) {
		margin-bottom: 1rem;
		text-align: left;
	}
}

.section-about .item .desc {
	margin-bottom: 60px;
}

.section-about .item .desc i {
	margin-right: 5px;
}

.section-about .item .quote {
	position: relative;
}

.section-about .item .quote .quote-profile {
	position: absolute;
	left: 0;
}

.section-about .item .quote .quote-content {
	margin-left: 90px;
	background-color: #f7f8f9;
	border-left: 4px solid #e5e5e5;
	border-radius: 4px;
	font-size: 14px;
	padding: 15px;

	&:before {
		content: '';
		display: block;
		width: 0;
		height: 0;
		border-top: 10px solid transparent;
		border-bottom: 10px solid transparent;
		border-right: 10px solid #e5e5e5;
		position: absolute;
		left: 80px;
		top: 25px;
	}
}

.section-about .item .quote blockquote {
	border-left: none;
	font-style: italic;
	color: #666;
	font-size: 16px;
	padding: 0;
	margin-bottom: 10px;
}

.section-about .item .quote .source {
	color: #999;
	font-size: 13px;
	margin-bottom: 0;
}

.section-about .item .quote .source a {
	color: #999;

	&:hover {
		color: #808080;
	}
}

.section-about .figure {
	text-align: center;
}

.section-about .figure .figure-caption {
	margin-top: 12px;
	font-size: 13px;
	text-align: center;
	color: #b3b3b3;
}

.section-about .figure .figure-caption a {
	color: #b3b3b3;

	&:hover {
		color: $brand-primary;
	}
}

.section-about .figure img {
	margin-left: auto;
	margin-right: auto;
}

.section-about .control {
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%,-50%);
}

.section-about .control .play-trigger {
	color: rgba(255, 255, 255, .9);
	background-color: rgba(0, 0, 0, .3);
	position: relative;
	width: 60px;
	height: 60px;
	border: 4px solid #fff;
	border-radius: 50%;
	margin: 0 auto;
	text-align: center;
	transition: all .2s ease-out;

	&:hover {
		color: rgba(255, 255, 255, 1);
		background-color: rgba(0, 0, 0, .6);
	}

	&:active,
	&:focus {
		outline: none;
	}
}

.section-about .control .play-trigger i {
	font-size: 26px;
	position: relative;
	left: 2px;
}

.section-about .feature-lead .title {
	margin-bottom: 1rem;
	font-size: 1rem;
	text-transform: none;
}

/* Testimonials */
.section-testimonials {
	padding-bottom: 6rem;
}

.section-testimonials .carousel-indicators {
	bottom: -60px;
	z-index: auto;
}

.section-testimonials .carousel-indicators li {
	background-color: #ccc;
	border: none;
	margin: 1px;
}

.section-testimonials .carousel-indicators li.active {
	background-color: lighten($brand-primary, 10%);
	border: none;
	width: 10px;
	height: 10px;
	margin: 1px;
}

.section-testimonials .carousel-cell .profile {
	position: absolute;
	left: 0;

	@media screen and (max-width: 767px) {
		position: static;
		margin: 0 auto;
		text-align: center;
		margin-bottom: 30px;
	}
}

.section-testimonials .carousel-cell .profile img {
	margin: auto;
}

.section-testimonials .carousel-cell .content {
	@media screen and (max-width: 767px) {
		text-align: center;
	}

	@media (min-width: 768px) {
		padding-left: 210px;
	}
}

.section-testimonials .carousel-cell blockquote {
	font-size: 20px;
	font-weight: 300;
	border-left: 0;
	padding: 0;
	position: relative;
}

.section-testimonials .carousel-cell blockquote i {
	position: absolute;
	color: #ef7d69;
	font-size: 2.875rem;
	top: -5px;
	left: -60px;

	@media screen and (max-width: 767px) {
		display: none;
	}
}

.section-testimonials .carousel-cell .source {
	color: $brand-primary;
}

.section-testimonials .carousel-cell .source .title {
	font-size: 16px;
	color: #666;
}

/* Press */
.section-press .press-list {
	margin-bottom: 1rem;
	text-align: center;
}

.section-press .press-list.last {
	margin-bottom: 3.75rem;
}

.section-press .press-list li {
	display: block;
	height: 80px;
}

.section-press .press-list li a {
	display: block;
	color: inherit;
	opacity: .6;

	&:hover {
		opacity: 1;
	}
}

.section-press .press-lead .title {
	margin-bottom: .5rem;
	font-size: 1.125rem;
	text-transform: none;
}

/* Section CTA */
.section-cta {
	padding-top: 6rem;
	padding-bottom: 6rem;
	border: none;
}

.section-cta .intro {
	margin-bottom: 1.5rem;
}

.section-cta .counting {
	font-weight: 700;
	opacity: 1;
}

.section-promo,
.bg-img-promo {
	height: 720px;
}

/* Section on background */
.section-on-bg {
	position: relative;
	z-index: 10;
}

.section-on-bg .caption {
	padding-bottom: 60px;

	@media screen and (min-width: 992px) {
		padding-top: 80px;
	}
}

