.searchbox-container {
	position: relative;
	width: 100%;

	@media screen and (min-width: 768px) {
		position: absolute;
	}
}

.searchbox-container .searchbox {
	min-width: 38px;
	height: 38px;
	position: relative;

	@media screen and (max-width: 767px) {
		width: 100%;
		top: inherit;
		float: none;
		margin-bottom: 30px;
	}

	@media screen and (min-width: 768px) {
		float: right;
		top: -34px;
		right: 30px;
		width: 0;
		transform: translateY(-50%);
		transition: width .3s;
	}
}

.searchbox-container .searchbox.searchbox-open {
	width: 100%;
}

/* Search Form */
.searchbox-icon,
.searchbox-submit {
	width: 38px;
	height: 38px;
	display: block;
	position: absolute;
	top: 0;
	font-size: 16px;
	right: 0;
	padding: 0;
	margin: 0;
	border: 0;
	outline: 0;
	line-height: 2.4;
	text-align: center;
	cursor: pointer;
	color: $brand-primary;
	background-color: #fff;

	@media screen and (max-width: 767px) {
		background-color: $brand-primary;
		color: #fff;
		border-top-right-radius: 4px;
		border-bottom-right-radius: 4px;
		border-bottom-left-radius: 0;
		border-top-left-radius: 0;
		background-clip: padding-box;
	}
}

.searchbox-icon {
	@media screen and (max-width: 767px) {
		display: none;
	}

	&:hover {
		color: darken($brand-primary, 10%);

		@media screen and (max-width: 767px) {
			color: #fff;
		}
	}
}

.searchbox-submit {
	font-size: 14px;
	font-family: FontAwesome;
	content: '\f002';
}

.searchbox-container .searchbox.searchbox-open .searchbox-icon,
.searchbox-container .searchbox.searchbox-open .searchbox-submit {
	background-color: $brand-primary;
	color: #fff;
	border-top-right-radius: 4px;
	border-bottom-right-radius: 4px;
	border-bottom-left-radius: 0;
	border-top-left-radius: 0;
}

.searchbox-container .searchbox .searchbox-input {
	position: absolute;
	top: 0;
	right: 0;
	border: none;
	outline: none;
	background-color: #f7f8f9;
	border: 1px solid #eee;
	width: 100%;
	height: 38px;
	margin: 0;
	padding-left: 20px;
	font-size: 13px;

	@media screen and (max-width: 767px) {
		position: static;
	}
}

.searchbox-container .searchbox .searchbox-input:-webkit-input-placeholder {
	/* WebKit browsers */
	color: #a6a6a6;
}

.searchbox-container .searchbox .searchbox-input:-moz-placeholder {
	/* Mozilla Firefox 4 to 18 */
	color: #a6a6a6;
}

.searchbox-container .searchbox .searchbox-input:-moz-placeholder {
	/* Mozilla Firefox 19+ */
	color: #a6a6a6;
}

.searchbox-container .searchbox .searchbox-input:-ms-input-placeholder {
	/* Internet Explorer 10+ */
	color: #a6a6a6;
}

/* Blog category page */
