/* Pagination */
.pagination {
	margin-top: 30px;
	margin-bottom: 60px;
	font-size: 14px;
	margin-left: auto;
	margin-right: auto;
}

.pagination li a {
	border-radius: 4px;
	border: none;
	margin-right: 5px;
	color: $brand-primary;

	&:hover {
		color: #da3518;
		color: darken($brand-primary, 7.5%);
	}
}

.pagination > .active > a,
.pagination > .active > span {
	&,
	&:hover,
	&:focus {
		background-color: $brand-primary;
		border: none;
	}
}

.pagination > li > a,
.pagination > li > span {
	&:hover,
	&:focus {
		background-color: rgba($brand-primary, .2);
		border: none;
	}
}

.pagination > li:first-child > a,
.pagination > li:first-child > span,
.pagination > li:last-child > a,
.pagination > li:last-child > span {
	border-radius: 4px;
	background: none;
}

