/* Modal */
.modal.modal-video .modal-dialog {
	width: 760px;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

.modal.modal-video .modal-body {
	padding: 0;
	padding-top: 0;
}

.modal.modal-video .modal-header {
	border: none;
	padding: 0;
	position: relative;
}

.modal.modal-video .modal-content {
	background: none;
	border: none;
	box-shadow: none;
}

.modal.modal-video button.close {
	font-size: 36px;
	font-weight: 300;
	text-shadow: none;
	color: #fff;
	opacity: 1;
	background: none;
	position: absolute;
	right: 0;
	bottom: 5px;
	z-index: 10;

	&:hover {
		color: $brand-primary;
		opacity: 1;
	}
}

.modal-backdrop.in {
	opacity: .9;
}

/* Animations Default State */
.js-animate,
.js-animate-children > * {
	opacity: 0;
	position: relative;
}

/* Configure Style (Remove in production) */
.config-wrapper {
	position: absolute;
	top: 100px;
	right: 0;
	z-index: 100;
}

.config-wrapper-inner {
	position: relative;
}

#config-trigger {
	display: block;
	position: absolute;
	background-color: #eee;
	color: inherit;
	text-align: center;
	border: solid #e5e5e5;
	border-width: 1px 0 1px 1px;
	border-radius: 4px 0 0 4px;
	left: 0;
	transform: translateX(0);
	transition: all .4s ease-out;

	&:hover,
	&:focus,
	&:active {
		outline: none;
		text-decoration: none;
		color: inherit;
	}
}

#config-trigger.loaded {
	transform: translateX(-100%);
}

.config-trigger i {
	font-size: 22px;
	padding: .5rem;
	animation: rotate 10s infinite forwards;
}

.config-trigger:hover i {
	color: $darkBlue;
}

.config-trigger.close {
	display: none;
	position: absolute;
	top: 0;
	right: 0;
	left: auto;
	color: #fff;
}

.config-trigger.close i {
	color: $darkBlue;
}

.config-panel {
	width: 170px;
	display: none;
	background-color: #eee;
	padding: 1rem;
	border: 1px solid #e5e5e5;
	border-radius: 0 0 0 4px;
}

.switcher-title {
	margin-top: -.125rem;
	margin-bottom: .5rem;
	font-size: 1rem;
	line-height: 1;
	text-align: center;
}

.config-panel h5 {
	margin-top: 1rem;
	margin-bottom: 1rem;
}

@keyframes rotate {
	0% {
		transform: rotate(0deg);
	}

	50% {
		transform: rotate(0deg);
	}

	95% {
		transform: rotate(0deg);
	}

	100% {
		transform: rotate(180deg);
	}
}
